'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { 
  Calendar, 
  Users, 
  Clock, 
  BarChart3, 
  Settings, 
  Bell,
  Home,
  DollarSign,
  Shield,
  Link2,
  TrendingUp,
  BookOpen,
  Award,
  Bot,
  Headphones
} from 'lucide-react';
import { cn } from '@/lib/utils';

const navigation = [
  { name: 'Home', href: '/', icon: Home },
  { name: 'Dashboard', href: '/dashboard', icon: BarChart3 },
  { name: 'Calendar', href: '/calendar', icon: Calendar },
  { name: 'Employees', href: '/employees', icon: Users },
  { name: 'Analytics', href: '/analytics', icon: TrendingUp },
  { name: 'Payroll', href: '/payroll', icon: DollarSign },
  { name: 'Recognition', href: '/recognition', icon: Award },
  { name: 'Training', href: '/training', icon: BookOpen },
  { name: 'AI Assistant', href: '/ai-assistant', icon: Bot },
  { name: 'Compliance', href: '/compliance', icon: Shield },
  { name: 'Integrations', href: '/integrations', icon: Link2 },
  { name: 'Support', href: '/support', icon: Headphones },
  { name: 'Time Off', href: '/time-off', icon: Clock },
  { name: 'Settings', href: '/settings', icon: Settings },
];

export default function Sidebar() {
  const pathname = usePathname();

  return (
    <div className="flex flex-col w-64 bg-gray-900 min-h-screen">
      <div className="flex items-center justify-center h-16 bg-gray-800">
        <span className="text-white text-xl font-bold">ðŸ“… Scheduler</span>
      </div>
      
      <nav className="flex-1 px-4 py-6 space-y-2">
        {navigation.map((item) => {
          const isActive = pathname === item.href;
          const Icon = item.icon;
          
          return (
            <Link
              key={item.name}
              href={item.href}
              className={cn(
                'flex items-center px-4 py-3 text-sm font-medium rounded-lg transition-colors',
                isActive
                  ? 'bg-blue-600 text-white'
                  : 'text-gray-300 hover:bg-gray-800 hover:text-white'
              )}
            >
              <Icon className="mr-3 h-5 w-5" />
              {item.name}
            </Link>
          );
        })}
      </nav>

      <div className="px-4 py-4 border-t border-gray-800">
        <button className="flex items-center w-full px-4 py-3 text-sm font-medium text-gray-300 rounded-lg hover:bg-gray-800 hover:text-white transition-colors">
          <Bell className="mr-3 h-5 w-5" />
          Notifications
          <span className="ml-auto bg-red-500 text-white text-xs rounded-full px-2 py-1">
            3
          </span>
        </button>
      </div>
    </div>
  );
}
